@import "../../styles/variables";

@-webkit-keyframes slide-bottom {
  0% {
    -webkit-transform: translateY(-25%);
    transform: translateY(-25%);
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@keyframes slide-bottom {
  0% {
    -webkit-transform: translateY(-25%);
    transform: translateY(-25%);
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

.app__navbar {
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: $color-black;

  padding: 1rem 2rem;
  z-index: 100;
}
.app__navbar_logo {
  display: flex;
  justify-content: center;
  align-items: center;
}
.app__navbar_logo img {
  width: 150px;
}
.app__navbar_links {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;

  list-style: none;
}

.app__navbar_links li {
  margin: 0 1rem;
  cursor: pointer;
}

.app__navbar_links li:hover {
  color: $color-grey;
}
.p__opensans {
  font-family: $font-alt;
  color: $color-white;
  font-weight: 400;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  line-height: 28px;
  font-size: 16px;
}
.app__navbar_login {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.app__navbar_login a {
  margin: 0 1rem;
  text-decoration: none;
  transition: 0.5s ease;
}

.app__navbar_login a:hover {
  border-bottom: 1px solid $color-golden;
}

.app__navbar_login div {
  width: 1px;
  height: 30px;
  background: $color-grey;
}
.app__navbar_smallscreen {
  display: none;
}
.app__navbar_smallscreen_overlay {
  display: flex;
  justify-content: center;
  align-items: center;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: $color-black;
  transition: 0.5s ease;

  flex-direction: column;
  z-index: 5;
}
.slide_bottom {
  -webkit-animation: slide-bottom 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: slide-bottom 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}
.app__navbar_smallscreen_overlay .overlay__close {
  font-size: 27px;
  color: $color-golden;
  cursor: pointer;

  position: absolute;
  top: 20px;
  right: 20px;
}
.app__navbar_smallscreen_links {
  list-style: none;
}

.app__navbar_smallscreen_links li {
  margin: 2rem;
  cursor: pointer;
  color: $color-golden;
  font-size: 2rem;
  text-align: center;
  font-family: $font-base;
}

.app__navbar_smallscreen_links li:hover {
  color: $color-white;
}

@media screen and (min-width: 2000px) {
  .app__navbar_logo img {
    width: 210px;
  }
}

@media screen and (max-width: 1150px) {
  .app__navbar_links {
    display: none;
  }

  .app__navbar_smallscreen {
    display: flex;
  }
}

@media screen and (max-width: 650px) {
  .app__navbar {
    padding: 1rem;
  }

  .app__navbar_login {
    display: none;
  }

  .app__navbar_logo img {
    width: 110px;
  }
}
